import * as Yup from "yup";

const schemaPFC = Yup.number()
  .min(0, "–ú–∞–ª–æ")
  .max(100, "–ú–Ω–æ–≥–æ")
  .required("–ù—É–∂–Ω–æ");

export const mealsValidationSchema = (form: "create" | "edit") => {
  return Yup.object({
    name: Yup.string()
      .min(4, "–°–ª–∏—à–∫–æ–º –∫–æ—Ä–æ—Ç–∫–æ–µ –Ω–∞–∑–≤–∞–Ω–∏–µ")
      .max(100, "–°–ª–∏—à–∫–æ–º –¥–ª–∏–Ω–Ω–æ–µ –Ω–∞–∑–≤–∞–Ω–∏–µ")
      .required("–ù–∞–∑–≤–∞–Ω–∏–µ –Ω–µ –º–æ–∂–µ—Ç –±—ã—Ç—å –ø—É—Å—Ç—ã–º"),
    startWeight: Yup.number()
      .max(1000, "–ú–Ω–æ–≥–æ–≤–∞—Ç–æ")
      .positive("?")
      .required("–ù—É–∂–Ω–æ"),
    protein: schemaPFC,
    fat: schemaPFC,
    carb: schemaPFC,
    weight: Yup.number().max(1000, "–ú–Ω–æ–≥–æ–≤–∞—Ç–æ").positive("?").required("–ù—É–∂–Ω–æ"),
    ...(form === "edit"
      ? {
          // max(31, "–ù–µ—Ç")
          day: Yup.number()
            .min(1, "–ù–µ—Ç")
            .required("–ù—É–∂–Ω–æ")
            .when(["month", "year"], {
              is: (month: number, year: number) =>
                month === new Date().getMonth() + 1 &&
                year === new Date().getFullYear(),
              then: Yup.number().max(new Date().getDate(), "–ë—É–¥—É—â–µ–µ"),
              otherwise: Yup.number().when("month", {
                is: (month: number) =>
                  month === 1 ||
                  month === 3 ||
                  month === 5 ||
                  month === 7 ||
                  month === 8 ||
                  month === 10 ||
                  month === 12,
                then: Yup.number().max(31, "–ù–µ—Ç"),
                otherwise: Yup.number().when("month", {
                  is: (month: number) =>
                    month === 4 || month === 6 || month === 9 || month === 11,
                  then: Yup.number().max(30, "–ù–µ—Ç"),
                  otherwise: Yup.number().when("year", {
                    is: (year: number) =>
                      (year % 4 === 0 && year % 100 !== 0) || year % 400 === 0,
                    then: Yup.number().max(29, "–ù–µ—Ç"),
                    otherwise: Yup.number().max(28, "–ù–µ—Ç"),
                  }),
                }),
              }),
            }),
          month: Yup.number()
            .min(1, "–ù–µ—Ç")
            .required("–ù—É–∂–Ω–æ")
            .when("year", {
              is: (year: number) => year === new Date().getFullYear(),
              then: Yup.number().max(new Date().getMonth() + 1, "–ë—É–¥—É—â–µ–µ"),
              otherwise: Yup.number().max(12, "–ù–µ—Ç"),
            }),
          year: Yup.number()
            .min(2019, "?")
            .max(new Date().getFullYear(), "–ë—É–¥—É—â–µ–µ")
            .required("–ù—É–∂–Ω–æ"),
        }
      : {}),
  });
};

export const profileValidationSchema = Yup.object({
  name: Yup.string()
    .min(2, "–°–ª–∏—à–∫–æ–º –∫–æ—Ä–æ—Ç–∫–æ–µ –∏–º—è")
    .max(30, "–°–ª–∏—à–∫–æ–º –¥–ª–∏–Ω–Ω–æ–µ –∏–º—è")
    .required("–ò–º—è –Ω–µ –º–æ–∂–µ—Ç –±—ã—Ç—å –ø—É—Å—Ç—ã–º"),
  age: Yup.number().min(13, "–û—Ç 13").max(80, "–î–æ 80").required("–ù—É–∂–Ω–æ"),
  stature: Yup.number().min(100, "–ú–∞–ª–æ").max(300, "–ú–Ω–æ–≥–æ").required("–ù—É–∂–Ω–æ"),
  weight: Yup.number().min(40, "–ú–∞–ª–æ").max(300, "–ú–Ω–æ–≥–æ").required("–ù—É–∂–Ω–æ"),
  sex: Yup.number().required("?"),
  activityLevel: Yup.number().required("?"),
  target: Yup.number().required("?"),
});
