import { useFormik } from "formik";
import { useMemo } from "react";
import { useDispatch } from "react-redux";
import Input from "../../components/Input";
import { addMeal } from "../../redux/mealSlise";
import { calcCaloriesEnding } from "../../utils/lingvo";
import { prepareValues } from "../../utils/math";
import { CalculatorFormProps, MealsInputValues } from "../../utils/types";
import { onKeyPress } from "../../utils/utils";
import { mealsValidationSchema } from "../../utils/validation";
import {
  Flex,
  FormContainer,
  Grid,
  SaveButton,
  Text,
  Title,
} from "./Form.styles";

function Form({ visible }: CalculatorFormProps) {
  const dispatch = useDispatch();

  const formik = useFormik({
    initialValues: {
      name: "",
      startWeight: 100,
      protein: "",
      fat: "",
      carb: "",
      weight: 100,
    } as MealsInputValues,
    validationSchema: mealsValidationSchema("create"),
    onSubmit: (values, { resetForm }) => {
      dispatch(addMeal({ ...(values as any), calories }));
      resetForm();
    },
  });

  const { name, startWeight, protein, fat, carb, weight } = formik.values;

  const calories = useMemo(() => prepareValues(formik.values), [formik.values]);

  return (
    <FormContainer onSubmit={formik.handleSubmit} visible={visible}>
      <Input
        formik={formik}
        value={name}
        name="name"
        placeholder="–ù–∞–∑–≤–∞–Ω–∏–µ –ø—Ä–æ–¥—É–∫—Ç–∞"
        type={"text"}
      />
      <Flex>
        <Text>–ë–ñ–£ –Ω–∞</Text>
        <Input
          formik={formik}
          value={startWeight}
          name="startWeight"
          placeholder="-–ì—Ä–∞–º–º—ã"
          type={"number"}
          onKeyPress={(evt) => onKeyPress(evt, formik)}
          startWeight={true}
        />
        <Text>–≥—Ä–∞–º–º</Text>
      </Flex>

      <Grid>
        <Input
          formik={formik}
          value={protein}
          name="protein"
          placeholder="–ë–µ–ª–∫–∏"
          type={"number"}
          onKeyPress={(evt) => onKeyPress(evt, formik)}
        />
        <Input
          formik={formik}
          value={fat}
          name="fat"
          placeholder="–ñ–∏—Ä—ã"
          type={"number"}
          onKeyPress={(evt) => onKeyPress(evt, formik)}
        />
        <Input
          formik={formik}
          value={carb}
          name="carb"
          placeholder="–£–≥–ª–µ–≤–æ–¥—ã"
          type={"number"}
          onKeyPress={(evt) => onKeyPress(evt, formik)}
        />
      </Grid>
      <Title>–í–µ—Å –ø—Ä–æ–¥—É–∫—Ç–∞:</Title>
      <Input
        formik={formik}
        value={weight}
        name="weight"
        placeholder="–ì—Ä–∞–º–º—ã"
        type={"number"}
        onKeyPress={(evt) => onKeyPress(evt, formik)}
      />
      <SaveButton
        type="submit"
        aria-label="–î–æ–±–∞–≤–∏—Ç—å –∑–∞–ø–∏—Å—å"
        disabled={!formik.isValid}
      >
        {` –î–æ–±–∞–≤–∏—Ç—å ${
          calories === 0 || Number.isNaN(calories)
            ? `–Ω–µ–º–Ω–æ–≥–æ –∫–∏–ª–æ–∫–∞–ª–æ—Ä–∏–π`
            : `${calories} ${calcCaloriesEnding(calories)}`
        }`}
      </SaveButton>
    </FormContainer>
  );
}

export default Form;
